import { Injectable } from '@angular/core';
import { Task } from '../interfaces/task';

@Injectable({
  providedIn: 'root'
})
export class TasksService {

  tasks: Array<Task> = [
{
  title: "1",
  description: ''
}
  ]
  constructor() { 
    let savedTasks = localStorage.getItem("title")
    savedTasks? this.tasks = JSON.parse(savedTasks): this.tasks = []
  }
  deleteTask(i){
    this.tasks.splice(i,1);
    this.saveAll()
  }
  saveTask(title, description){
    this.tasks.push({
      title,
      description
    })
    this.saveAll()
  }

  editTask(i,data){
  this.tasks[i] = data;
  this.saveAll();
  }
  saveAll(){
    localStorage.setItem("title",JSON.stringify(this.tasks))
  }
}
